import React from "react";
import Container from "react-bootstrap/Container";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

export default function PriseDeVue() {
  return (
    <Container fluid>
      <Row>
        <Col xs={0} sm={2}></Col>
        <Col xs={true}>
          <div class="paragraphe">
            <h1>Collecte d'images</h1>
            <p>
              Avant de pouvoir crer un modle 3D par photogrammtrie, il faut
              imprativement une source de donnes. Ici, notre source de donnes
              sera des images. Elles peuvent tre prises par diffrents
              appareils comme un smartphone, un appareil photo ou encore un
              drone en fonction du besoin.
            </p>
            <h2>1. Photogrammtrie a partir dun smartphone</h2>
            <p>
              Il est possible de modliser simplement un modle 3D juste 
              partir de la camra dun smartphone et dun ordinateur performant.
              Il faut une bonne dfinition dimage (au minimum 10 millions de
              pixels par image) et environ 75 images pour un modle de 15  30
              cm de hauteur. Il est trs important de capturer les images selon
              une mthode spcifique. Il faudra tourner autour de lobjet en
              question. Chaque image devra se chevaucher avec la prcdente sur
              environ 60  80% de limage. Cela permettra de trouver des points
              similaires entre chaque image.
            </p>
            <div class="backImg">
              <img src="images/PriseDeVueImg1.jpg" />
              <p class="legende">
                Ici, on voit sous forme de schma le processus de prise de
                photos avec le smartphone.{" "}
              </p>
            </div>
            <p>
              Toutes ses images permettront, une fois importes dans le logiciel
              et traites correctement de gnrer un objet 3D qui est par
              exemple imprimable ou utilisable dans un jeu vido.
            </p>
            <h2>2. Photogrammtrie par prise de vue arienne</h2>
            <p>
              On peut galement crer un modle 3D  partir dimages prises par
              un drone. Il est possible de crer un plan non dtaill dun
              ensemble ou avoir une modlisation 3D trs prcise dun batiment
              ou une zone. Il existe des applications qui permettent de faire de
              la prise de vue par drone de faon semi automatise. Jai utilis
              lors de mes tests lapplication PIX4D capture.
            </p>
            <div class="backImg">
              <Row>
                <Col xs={true}>
                  <img src="images/PriseDeVueImg2.jpg" />
                </Col>
                <Col xs={true}>
                  <img src="images/PriseDeVueImg3.jpg" />
                  <p class="legende">
                    Capture dcran de lapplication aprs la prise de vue, qui
                    montre toutes les images prises dans le quadrillage, ainsi
                    que le compte rendu de lapplication
                  </p>
                </Col>
              </Row>
            </div>
            <br />
            <p>
              Lutilisation est assez simple. Il faut dlimiter une zone et
              altitude de travail, ensuite le drone quadrillera la zone en
              prenant de nombreuses photos. Cela permet de crer un modle assez
              prcis de la zone dlimite dans lapplication.
            </p>
            <div class="backImg">
              <img src="images/PriseDeVueImg4.jpg" />
              <p class="legende">
                Rendu aprs traitement par le logiciel des 127 images prises
                ci-dessus.{" "}
              </p>
            </div>
            <p>
              Dans certains cas, cette modlisation suffit. Mais parfois, il
              peut tre ncessaire davoir un modle gnr plus prcis et avec
              de belles textures. Pour cela, certains logiciels comme Agisoft
              Metashape permettent de gnrer un plan de vol au drone aprs un
              premier jet de modlisation. Le plan de vol tient compte des
              contraintes du terrain, et permet au drone de sapprocher au plus
              prs des structures sans entrer en collision avec. Il est
              recommand de ne pas sapprocher a moins de 3 mtres dun objet
              modlis dans le logiciel afin de garder une certaine marge de
              scurit entre le modle gnr et la ralit. Ce plan de vol plus
              dtaill permet de prendre beaucoup plus dimages et sous de
              nombreux angles. Il prsente de nombreux avantages et permet
              davoir un objet 3D final trs dtaill.
            </p>
            <div class="backImg">
              <img src="images/PriseDeVueImg5.jpg" />
              <p class="legende">
                Cette image montre le plan de vol dtaill modlis dans le
                logiciel Agisoft Metashape
              </p>
            </div>
            <h2>3. Conditions de russite de la prise de vue</h2>
            <p>
              Pour une bonne prise de vue, il faut absolument que certaines
              conditions soient runies. Il faut que la zone soit bien claire,
              avec (pour le drone) un luminosit extrieure leve et si
              possible peu de zones dombre. Les zones ombrages seront
              reportes sur le modle et peuvent donner un rendu mdiocre de la
              modlisation 3D. Il faut galement penser si le matriel le
              permet,  utiliser le format de fichier RAW afin davoir un
              maximum de donnes par image. Il est galement inutile dutiliser
              des filtres ou de retoucher les images avant traitement. Les
              images RAW sont celles qui contiennent le plus dinformations, et
              seront donc bien plus intressantes pour avoir de bons rsultats
              de modlisation.
            </p>
            <p>
              Il est galement possible dimprimer des modles 3D gnrs afin
              davoir de meilleures reprsentations visuelles de lespace, et
              ainsi crer des maquettes simplement.
            </p>
          </div>
        </Col>
        <Col xs={0} sm={2}></Col>
      </Row>
    </Container>
  );
}
